@use "../../scss/" as *;

.body-all {
    //-webkit-print-color-adjust: exact;
    width: 100%;
    margin: auto;
    background-color: #FDFAF6;
    border: 0px solid #064420
}

@media only screen and (min-width: 821px) {
    .body-all {
        //-webkit-print-color-adjust: exact;
        width: 50%;
        margin: auto;
        background-color: #FDFAF6;
        border: 1px solid #064420
    }

    .content-view-all {
    }
  }

.user-name {
    font-weight: 600;
    font-size: xx-large;
    color: #1B1A17;
    text-align: center;
    margin-bottom: 8px;
}

.avatar-view {
    width: 200px;
    height: 200px;
    margin: auto;
    margin-top: 32px;
    margin-bottom: 8px;
    border-radius: 100px;
    border: 2px solid #1B1A17;
    .avatar {
        width: 100%;
        height: 100%;
        border-radius: 50%;
    }
}

.user-title {
    margin-bottom: 4px;
    font-size: small;
    color: #1B1A17;
    text-align: center;
}

.section-view {
    padding: 16px;
    .title {
        font-weight: 600;
        font-size: x-large;
        color: #1B1A17;
        margin-bottom: 8px;
    }

    .text {
        color: #1B1A17;
        font-weight: 300;
        font-size: medium;
        padding-bottom: 8px;
        line-height: 1.4;
        padding-left: 16px;
    }

    .text-no-padding-left {
        padding-left: 0px;
    }

    .highlight {
        color: #1B1A17;
        font-style: italic;
    }

    .summary {
        color: #1B1A17;
        font-style: italic;
        font-size: medium;
        line-height: 1.5;
        margin-bottom: 16px;
        background-color: #FAF1E6;
        padding: 8px 16px 8px 16px;
        // box-shadow: 5px 5px #064420;
        // border: 1px solid #064420;
//         box-shadow: 20px 20px 29px -17px rgba(0,0,0,0.54);
// -webkit-box-shadow: 6px 5px 29px -17px rgba(0,0,0,0.54);
// -moz-box-shadow: 6px 5px 29px -17px rgba(0,0,0,0.54);
        border-radius: 10px;
        border: 1px solid #FAF1E6;
        -webkit-box-shadow: 2px 2px 0px 0px rgba(0,0,0,0.15); 
        box-shadow: 2px 2px 0px 0px rgba(0,0,0,0.15); 
    }

    .project-view {
        background-color: #FDFAF6;
        margin-bottom: 16px;
        padding: 16px;
        // box-shadow: 5px 5px #1B1A17;
        // border: 1px solid #1B1A17;
        -webkit-box-shadow: 2px 2px 0px 0px rgba(0,0,0,0.15); 
        box-shadow: 2px 2px 0px 0px rgba(0,0,0,0.15); 
        border-radius: 10px;
        border: 1px solid #FAF1E6;
        .name {
            font-weight: 600;
            font-size: large;
            line-height: 1.5;
        }

        .description {
            line-height: 1.3;
            padding-left: 8px;
            padding-top: 8px;
            font-style: italic;
            border-bottom: 1px dashed #fde6ca;
            padding-bottom: 8px;
        }

        .job-title {
            margin-top: 16px;
            font-weight: 600;
            font-size: medium;
        }

        .job-content {
            padding-left: 8px;
            padding-top: 8px;
            font-weight: 300;
            line-height: 1.3;
        }

        .sub-name {
            font-weight: 400;
            font-size: medium;
        }
    }

    .center-text {
        text-align: center;
    }
}

.all-projects {
    background-color: #FAF1E6;
}

.bottom-view {
    background-color: #FAF1E6;
    width: 100%;
    padding: 32px;
    border-top: 3px solid #FAF1E6;
    height: 64px;

    .text {
        color: #1B1A17;
        font-weight: 300;
        font-size: medium;
        padding-bottom: 16pxpx;
        text-align: center;
    }
}

.top-view {
    background-color: #FAF1E6;
    width: 100%;
    padding: 32px;
    //border-bottom: 1px solid #064420;
    margin-bottom: 32px;
}

.content-view {
    padding: 16px;
}